/***** Global Slide *****/
.slide-right,
.slide-left {
    width: 100%;
}

/***** Slide Right *****/
.slide-right {
    -webkit-animation: 3s slide-right;
            animation: 3s slide-right;
}
@-webkit-keyframes slide-right {
    from {
        margin-left: -100%;
    }
    to {
        margin-left: 0%;
    }
}
@keyframes slide-right {
    from {
        margin-left: -100%;
    }
    to {
        margin-left: 0%;
    }
}

/***** Slide Left *****/
.slide-left {
    -webkit-animation: 3s slide-left;
            animation: 3s slide-left;
}

@-webkit-keyframes slide-left {
    from {
        margin-left: 100%;
    }
    to {
        margin-left: 0%;
    }
}

@keyframes slide-left {
    from {
        margin-left: 100%;
    }
    to {
        margin-left: 0%;
    }
}

.slide-down {
    -webkit-animation: 1.5s slide-down;
            animation: 1.5s slide-down;
}
@-webkit-keyframes slide-down {
    from {
        margin-bottom: 100%;
    }
    to {
        margin-bottom: 0%;
    }
}
@keyframes slide-down {
    from {
        margin-bottom: 100%;
    }
    to {
        margin-bottom: 0%;
    }
}

.slide-top {
    -webkit-animation: 1.5s slide-top;
            animation: 1.5s slide-top;
}
@-webkit-keyframes slide-top {
    from {
        margin-top: 60%;
    }
    to {
        margin-top: 0%;
    }
}
@keyframes slide-top {
    from {
        margin-top: 60%;
    }
    to {
        margin-top: 0%;
    }
}

.fade-in {
    -webkit-animation: fadeIn ease 3s;
            animation: fadeIn ease 3s;
}
@-webkit-keyframes fadeIn {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}
@keyframes fadeIn {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}
